// 主页页面的页面路径
// 关联功能：打开的页面只有一个的时候右上角自动显示返回首页按钮，下面这个数组是排除显示返回首页的页面。
// 主页使用场景：小程序分享出去的页面，用户点击开是分享页面，很多情况下是没有返回首页按钮的
const mainPagePath = ['pages/index/index', 'pages/demo6'];
//返回首页的地址
const homePath = '/pages/index/index';
//白色表达值
const whiteList = ['#FFF', '#FFFFFF', 'white', 'rgb(255,255,255)', 'rgba(255,255,255,1)'];
export default {
	props: {
		//是否显示返回按钮
		// 1000 显示返回按钮
		// 2000 不显示返回按钮
		// 3000 自定义返回按钮方法，点击返回箭头后会发送一个backClick事件
		backState: {
			default: function() {
				return 1000;
			}
		},
		//是否显示返回首页按钮
		home: {
			type: Boolean,
			default: function() {
				return false;
			}
		},
		//导航背景色，支持渐变
		bgColor: {
			type: [String, Array],
			default: function() {
				return '#FFFFFF';
			}
		},
		// 导航背景色渐变角度
		bgColorAngle: {
			type: [String, Number],
			default: function() {
				return 90;
			}
		},
		//导航字体颜色，字体颜色为白色的时候会把手机状态栏设置为白色，否则为黑色
		fontColor: {
			type: String,
			default: function() {
				return '#000000';
			}
		},
		//标题是否居中
		titleCenter: {
			type: Boolean,
			default: function() {
				return true;
			}
		},
		//标题
		title: {
			type: String,
			default: function() {
				return '';
			}
		},
		//类型 fixed为固定 默认
		// ordinary 普通的 不固定
		// transparent 透明不固定的
		//transparentFixed  透明固定的
		type: {
			type: String,
			default: function() {
				return 'fixed';
			}
		},
		//透明固定的时候字体颜色
		transparentFixedFontColor: {
			type: String,
			default: function() {
				return '#000000';
			}
		},
		// 屏幕滑动距离顶部距离(透明固定导航比传)
		scrollTop: {
			type: Number,
			default: function() {
				return 0;
			}
		},
		// 是否显示阴影
		shadow: {
			type: Boolean,
			default: function() {
				return true;
			}
		},
	},
	data() {
		return {
			//当前页面是否是第一个页面
			firstPage: false,
			//透明度值
			transparentValue: 1,
			//标题
			navTitle: '',
			//字体色
			navFontColor: '#000000',
			//背景色
			navBgColor: 'none',
			//透明底字体色
			navTransparentFixedFontColor: '#000000',
			// 是否使用
			themeBgColor: false,
			// 导航栏高度
			statusBarHeight: 0,
			// 上次显示的导航栏颜色
			lastFrontColor:""
		};
	},
	computed: {
		back() {
			return this.backState == 1000 || this.backState == 3000;
		},
		//导航固定
		navFixed() {
			if (this.type == 'transparentFixed' || this.type == 'fixed') {
				return true;
			} else {
				return false;
			}
		},
		//导航底部线是否显示
		navShadow() {
			if(this.bgColor && typeof(this.bgColor) == "string"){
				return this.shadow && this.type !== 'transparent' && whiteList.includes(this.bgColor);
			}else{
				return false;
			}
		},
		//导航字体是否是白色颜色
		isWhite() {
			return whiteList.includes(this.navFontColor);
		},
		//右上角是否有两个按钮
		isTwoBtn() {
			return (this.backState == 1000 || this.backState == 3000) && this.home && !this.firstPage;
		}
	},
	watch: {
		title(val) {
			this.navTitle = val;
		},
		fontColor(val) {
			this.navFontColor = val;
			this.settingColor();
		},
		bgColor(val) {
			this.getNavBgColor(val);
		},
		transparentFixedFontColor(val) {
			this.navTransparentFixedFontColor = val;
		},
		scrollTop(val) {
			this.pageScroll({
				scrollTop: val
			}); 
		}
	},
	//第一次加载
	created() {
		this.navTitle = this.title;
		this.navFontColor = this.fontColor;
		this.getNavBgColor(this.bgColor);
		this.navTransparentFixedFontColor = this.transparentFixedFontColor;
		//获取手机状态栏高度
		this.statusBarHeight = uni.getSystemInfoSync()['statusBarHeight'];
		const _this = this;
		this.pageScroll({
			scrollTop: this.scrollTop
		});
		//获取所有页面
		let currentPages = getCurrentPages();
		let pageLen = currentPages.length;
		//判断是否是第一个页面，如果是有设置back为true的页面，将不显示返回箭头，而显示返回首页按钮
		if (pageLen == 1 && !mainPagePath.includes(currentPages[0].route)) {
			this.firstPage = true;
		}
	},
	//方法
	methods: {
		//返回上一页面
		onBackPage() {
			if (this.backState == 3000) {
				this.$emit('backClick');
			} else {
				uni.navigateBack();
			}
		},
		//返回首页
		onBackHome() {
			// uni.switchTab({
			// 	url: homePath
			// });
			uni.switchTab({
				url: homePath,
				success: function(e) {
					var page = getCurrentPages().pop();
					if (page == undefined || page == null) return;
					if( page.hasOwnProperty('onLoad') ){
						page.onLoad();
					}else{
						location.reload();
						
					}
					
				}
			});
		},
		pageScroll(e) {
			if (this.type == 'transparentFixed') {
				if (e.scrollTop && e.scrollTop > 0) {
					if (e.scrollTop > 180) {
						this.transparentValue = 1;
					} else {
						this.transparentValue = e.scrollTop / 180;
					}
				} else {
					this.transparentValue = 0;
				}
				this.settingColor();
			}
		},
		// 获取导航背景颜色
		getNavBgColor(val) {
			if (val == 'themeBgColor') {
				this.themeBgColor = true;
				this.navBgColor = 'none';
			} else if (this.type == 'transparent') {
				this.themeBgColor = false;
				this.navBgColor = 'none';
			} else {
				if (typeof val == 'string') {
					this.navBgColor = 'linear-gradient(90deg,' + val + ',' + val + ')';
				} else if (Array.isArray(val) && val.length >= 2) {
					let navBgColor = 'linear-gradient(' + this.bgColorAngle + 'deg';
					val.forEach(item => {
						if (typeof item == 'string') {
							navBgColor += ',' + item;
						} else if (typeof item == 'object') {
							navBgColor += ',' + item.color + ' ' + item.scale;
						}
					});
					navBgColor += ')';
					this.navBgColor = navBgColor;
				}
			}
		},
		//设置手机状态栏颜色
		settingColor() {
			let navColor = this.navFontColor;
			if (this.type == 'transparentFixed' && this.transparentValue <= 0.5) {
				navColor = this.navTransparentFixedFontColor;
			}
			let frontColor = "#000000";
			if (whiteList.includes(navColor)) {
				frontColor = '#ffffff';
			}
			if (this.lastFrontColor == frontColor) {
				return;
			}
			setTimeout(() => {
				this.lastFrontColor = frontColor;
				// 改变手机状态栏颜色
				uni.setNavigationBarColor({
					frontColor: frontColor,
					backgroundColor: "#FFFFFF"
				});
			}, 150);
		}
	}
};
